.Container {
    position: relative;
    font-family: 'Oswald', sans-serif;
    width: 100%;
    height: 100%;
    color: white;
}

.Content{
    position: absolute;
    top: 150px;
    margin: 0 50px;
    left: 0;
    right: 0;
    z-index: 10
}

.Home {
    position: absolute;
    top: 1200px;
    margin: 0 50px;
    left: 0;
    right: 0;
    z-index: 10
}

.ContentRel {
    position: relative;
    width: 100%;
    display: block;
    flex-direction: row;
}

.ContentRel h1 {
    margin: 0;
    font-size: 150px;
}

.ContentRel p {
    font-size: 20px;
    margin-top: 0px;
    margin-bottom: 20px;
}

@media (max-width: 600px) {
    .ContentRel h1 {
        margin: 0;
        font-size: 70px;
    }

    .Content{
        margin: 0 10px;
    }
}

.Topo {
    height: auto;
    z-index: 5;
    overflow: hidden;
}

.Topo1 {
    position: absolute;
    right: 0px;
    top: -5px;
    width: 550px;
}

.Topo2 {
    position: absolute;
    left: 0px;
    top: -5px;
    width: 450px;
}

.Topo3 {
    position: absolute;
    left: 0px;
    top: 750px;
    width: 550px;
}

@media (max-width: 1000px) {
    .Topo1{
        visibility: hidden;
    }
}

@media (max-width: 450px) {
    .Topo2 {
        width: 100%;
    }
}

@media (max-width: 550px) {
    .Topo3 {
        width: 100%;
    }
}

@media (max-width: 500px) {
    .Topo3 {
        top: 550px;
    }
}
