.SideDrawer {
    display: none;
    visibility: hidden;
}

.SideDrawer a {
    font-family: 'Oswald', sans-serif;
    text-decoration: none;
    outline:none;
    border: none;
    font-size: 30px;
    letter-spacing: 1px;
    color: white
}

.SideDrawer a:hover {
    color: rgb(136, 0, 0);
}

.Open {
    transform: translateX(0);
}

.Close {
    transform: translateX(-100%);
}

@media (max-width: 900px) {
    .SideDrawer {
        display: block;
        visibility: visible;
        height: 2000px;
        width: 280px;
        max-width: 70%;
        position: fixed;
        top: 49px;
        left: 0;
        -webkit-backdrop-filter: blur(5px);
        backdrop-filter: blur(5px);
        background-color: rgba(255, 255, 255, .3);
        align-items: center;
        z-index: 200;
        padding: 16px;
    }
}