.Toolbar {
    visibility: hidden;
    position: fixed;
    top: -50px;
}

.BackDrop {
    visibility: hidden;
}

@media (max-width: 900px) {
    .Toolbar {
        visibility: visible;
        height: 50px;
        width: 100%;
        position: fixed;
        top: 0px;
        left: 0;
        -webkit-backdrop-filter: blur(5px);
        backdrop-filter: blur(5px);
        background-color: rgba(255, 255, 255, .3);
        align-items: center;
        z-index: 200;
        color: white;
    }

    .BackDrop {
        visibility: visible;
        z-index: 100;
        position: fixed;
        height: 2000px;
        top: 0px;
        left: 0px;
        right: 0px;
        bottom: 0px;
        background-color: rgba(0, 0, 0, 0.5);
        cursor: pointer;
    }
}