.Main {
    text-align: left;
    margin: 0 10%; 
}

.Hide {
    display: none;
}

.Main h2 {
    font-size: 45px;
    cursor:pointer;
}

.Main h2:hover {
    color: rgb(136, 0, 0);
}

.Item {
    margin-bottom: 40px;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    background-color: rgba(255, 255, 255, .3); 
    z-index: 11;
    border: solid 2px white;
    border-radius: 5px;
    padding: 25px;
}

.Item h3 {
    font-size: 30px;
}

.Item li {
    font-size: 25px;
}

.PicBig {
    width: 800px;
    height: 100%;
    padding: 10px;
    border: solid 2px white;
    border-radius: 5px;
    margin: 10px;
}

.PicSmall {
    width: 400px;
    height: 100%;
    padding: 10px;
    border: solid 2px white;
    border-radius: 5px;
    margin: 10px;
}

.PicBox {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

@media (max-width: 1200px) {
    .PicBig {
        width: 100%;
    }
}

@media (max-width: 700px) {
    .PicSmall {
        width: 100%;
    }
}

@media (max-width: 900px) {
    .Main {
        margin: 0;
    }
}