h1, h2 {
    margin: 0;
}

.Title {
    font-size: 85px;
    font-weight: bold;
    letter-spacing: 5px;
    z-index: 10;
    position: absolute;
    top: 300px;
    left: 175px;
}

.Title h2 {
    letter-spacing: 4.5px;
    font-size: 30px;
}

.Title p {
    visibility: hidden;
}

@media (max-width: 1559px) {
    .Title{
        position: relative;
        left: auto;
    }
}

@media (max-width: 1250px) {
    .Title h1 {
        font-size: 60px;
    }

    .Title h2 {
        font-size: 20px;
    }
}

@media (max-width: 1000px) {
    .Title h2 {
        visibility: hidden;
    }

    .Title p {
        visibility: visible;
        font-size: 25px;
    }
}

@media (max-width: 500px) {
    .Title h1 {
        font-size: 35px;
    }

    .Title p {
        margin: 0px;
        font-size: 20px;
    }

}

@media (max-width: 400px) {
    .Title {
        top: 250px;
    }
}



.Main {
    height: 700px;
    width: 100%;
}

.ContentRel {
    position: relative;
    width: 100%;
    display: block;
    flex-direction: row;
}

.BigScreen {
    height: 700px;
    width: 70%;
    margin-right: 2%;
    border: solid 2px white;
    float: left;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    background-color: rgba(255, 255, 255, .3); 
}

.SidePanel {
    margin-left: 1%;
    float: right;
    width: 26%;
}

.SmallScreen {
    margin-bottom: 20px;
    height: 200px;
    border: solid 2px white;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    background-color: rgba(255, 255, 255, .3);
}

.Links {
    text-align: left;
    display: block;
}

.Links button {
    width: 100%;
    background-color:transparent;
    outline:none;
    border: none;
    color: white;
    font-weight: bold;
    text-align: left;
    margin-bottom: 20px;
    font-size: 30px;
}

.Links button:hover {
    color: rgb(136, 0, 0);
    cursor:pointer;
}