.InputElement {
    outline: none;
    color: white;
    border: 1px solid #ccc;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    background-color: rgba(255, 255, 255, .3);
    margin: 15px 0;
    font: inherit;
    padding: 6px 10px;
    display: block;
    width: 100%;
    box-sizing: border-box;
    resize: none;
    letter-spacing: 1px;
}

.Red {
    border: 2px solid red;
}

.Input textarea {
    height: 300px;
}

.Label {
    display: block;
    margin: 8px 0;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
}
