.Main {
    text-align: left;
    margin: 0 10%;
    border: solid 2px white;
    border-radius: 5px;
    padding: 25px;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    background-color: rgba(255, 255, 255, .3); 
    z-index: 11;
}

.Main h2 {
    font-size: 45px;
}

.Header {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-bottom: 20px;
    align-items: center;
}

.Item {
    margin-bottom: 100px;
}

.Info {
    padding: 0 20px;
}

.SubHeader {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.SubHeader h3 {
    font-size: 35px;
}

.SubHeader p {
    font-size: 30px;
}

.Item p {
    font-size: 25px;
}

.Info li {
    font-size: 20px;
}

.Pic {
    width: 200px;
}

.Skills {
    display: flex;
    flex-wrap: wrap;
    
}

.Skills h3 {
    margin: 0 15px;
    font-size: 20px;
}

@media (max-width: 900px) {
    .SubHeader {
        flex-direction: column;
    }

    .SubHeader h3 {
        margin: 5px 0;
    }

    .SubHeader p {
        margin: 5px 0;
    }

    .Main {
        margin: 0;
    }

    .Header {
        flex-direction: column;
    }
}
