.ToolBar {
    position: absolute;
    top: 1080px;
    left: 0;
    right: 0;
    border: solid 2px white;
    height: 80px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    background-color: rgba(255, 255, 255, .3); 
    margin: 0 50px;
    z-index: 11;
}

.ToolBarTop {
    top: 20px;
}

.ToolBar a {
    font-family: 'Oswald', sans-serif;
    color: white;
    border:none;
    background-color:transparent;
    text-decoration: none;
    outline:none;
    font-weight: bold;
    font-size: 25px;
    letter-spacing: 1px;
}

.ToolBar a:hover {
    color: rgb(136, 0, 0);
}

@media (max-width: 900px) {
    .ToolBar {
        visibility: hidden;
    }
}