.Form {
    display: flex;
    justify-content: center;
    width: 100%;
    margin-bottom: 20px;
}

.FormContents {
    width: 50%;
}

.Button {
    display: table-row;
    float: right;
}

.Button button {
    width: 100%;
    background-color: transparent;
    outline:none;
    border: none;
    color: white;
    font-weight: bold;
    text-align: right;
    margin-bottom: 20px;
    font-size: 30px;
}

.Button button:hover {
    color: rgb(136, 0, 0);
    cursor:pointer;
}

.ContactInfo {
    display: flex;
    justify-content: center;
    width: 100%;
    margin-bottom: 100px;
}

.ContactInfoContents {
    margin-top: 20px;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    background-color: rgba(255, 255, 255, .3);
    border: solid 1px white;
    width: 50%;
}

.ContactInfoContents h2 {
    margin: 20px 0;
    letter-spacing: 1px;
}

.ContactInfoContents a {
    text-decoration: none;
    outline:none;
    color: white;
}

.ContactInfoContents a:hover {
    color: rgb(136, 0, 0);
}

@media (max-width: 700px) {
    .ContactInfoContents h2{
        font-size: 15px;
    }
}

@media (max-width: 1200px) {
    .FormContents {
        width: 100%;
    }

    .ContactInfoContents {
        width: 100%;
    }    
}
