.Footer {
    margin-top: 300px;
    margin-bottom: 100px;
    bottom: 0;
    display: flex;
    justify-content: space-evenly;
    vertical-align: top;
    border-top: solid rgba(255, 255, 255, .3) 2px;
    left: 0;
    right: 0;
    z-index: 10;
    
}

.Footer button, .Footer a {
    font-family: 'Oswald', sans-serif;
    background-color:transparent;
    text-decoration: none;
    outline:none;
    border: none;
    font-weight: bold;
    font-size: 30px;
    letter-spacing: 1px;
    color: rgba(255, 255, 255, .3);
}

.Footer button:hover, a:hover {
    color: white;
    cursor: pointer;
}

@media (max-width: 1000px) {
    .Footer {
        justify-content: center;
        flex-direction: column;
    }
}